import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Pagination from '@material-ui/lab/Pagination';
import PaginationItem from '@material-ui/lab/PaginationItem';
import { makeStyles } from "@material-ui/core/styles";

import MenuCategoryClt from '../../../components/MenuCategoryClt'
import Search from '../../../components/Search';
import BookProduto from '../../../components/BookProduto';
import { useBookData } from '../../../context/bookContext'

import './styles.css'

const useStyles = makeStyles(() => ({
  root: {
    "& .Mui-selected": {
      background: "#00cc6d",
      color: "white",
      "&:hover": {
        background: "#c4f5d0",
        color: "black"
      }
    },
    "& .MuiPaginationItem-page:hover": {
      background: "#c4f5d0",
      opacity: 0.9
    }
  }
}));

const BookCliente = () => {
  const {
    contextBooksData,
    setContextBooksData,
    createURLtoFetchBooks,
    fetchBooksDataPagination
  } = useBookData()

  useEffect(() => {
    createURLtoFetchBooks()
  }, [createURLtoFetchBooks])

  useEffect(() => {
    fetchBooksDataPagination(contextBooksData.BaseToFetchBooksURL)
  }, [contextBooksData.BaseToFetchBooksURL, fetchBooksDataPagination])

  const classes = useStyles();

  return (
    <div className="BookCliente">
      <div className="Container">
        <div className="Top-Menu">
          <div className="CategoryMenu">
            <MenuCategoryClt title="Book" />
            <div className="Search">
              {contextBooksData.books && <Search data={[contextBooksData.books, setContextBooksData]} />}
            </div>
          </div>
        </div>

        <div className="Content-App">
          <div className="content-midia-views">
            {contextBooksData.books && contextBooksData.view.map((book, index) => (
              <Link key={index} to={`/cltbookview`} onClick={() => {
                setContextBooksData(state => ({ ...state, bookView: book }))
                localStorage.setItem('@greenishBook:BOOK_VIEW', JSON.stringify(book))
              }}>
                <BookProduto key={index} src={book.media} title={book.name} />
              </Link>
            ))}
          </div>
          <div className="Content-Pagination">
            <Pagination
              onChange={(_, v) => setContextBooksData(state => ({ ...state, stateURL: { limit: 10, offset: v } }))}
              page={contextBooksData.stateURL.offset}
              className={classes.root}
              count={contextBooksData.lengthPagination}
              boundaryCount={2}
              renderItem={item => (
                <PaginationItem
                  style={{
                    fontSize: 12,
                    fontWeight: 'bold',
                  }}
                  {...item} />
              )}
            />
          </div>
        </div>
      </div>
    </div>
  )
}

export default BookCliente