import { createContext, useCallback, useContext, useState } from "react";
import api, { URL_ACCESS } from "../services/api";

export const BookContext = createContext({})

export const BookDataProvider = (props) => {
  const [state, setState] = useState({
    books: [],
    bookView: [],
    view: [],
    baseToFetchBooksURL: null,
    stateURL: {
      limit: 8,
      offset: 1,
    },
    lengthPagination: 0,
  })

  const createURLtoFetchBooks = useCallback(() => {
    let NewURL = `${URL_ACCESS}/api/books/?`
    const cases = {
      'limit': `limit=${state.stateURL.limit}`,
      'offset': `&offset=${(Number(state.stateURL.offset) - 1) * state.stateURL.limit}`,
    }
    NewURL += Number(state.stateURL.limit) !== -1 ? cases['limit'] : ''
    NewURL += Number(state.stateURL.offset) !== -1 ? cases['offset'] : ''

    setState(state => ({ ...state, BaseToFetchBooksURL: NewURL }))
  }, [state.stateURL])

  const fetchBooksDataPagination = useCallback((bookPagination) => {
    if (!bookPagination) { return }
    api.get(bookPagination)
      .then(response => {
        const data = response.data
        const lengthPagination = Math.floor(data.count / state.stateURL.limit)
        const lengthBook = data.count % state.stateURL.limit

        lengthBook > 0
          ? setState(state => ({
            ...state,
            lengthPagination: lengthPagination + 1
          }))
          : setState(state => ({
            ...state,
            lengthPagination: lengthPagination
          }))

        setState(state => ({ ...state, books: data.results, view: data.results }))
      })
      .catch(err => {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      })
  }, [state.stateURL.limit])

  return (
    <BookContext.Provider value={{
      contextBooksData: state,
      setContextBooksData: setState,
      createURLtoFetchBooks,
      fetchBooksDataPagination
    }}>
      {props.children}
    </BookContext.Provider>
  )
}

export const useBookData = () => {
  const context = useContext(BookContext)
  if (context === undefined) {
    throw new Error('useData necessita ser utilizado dentro de DataProvider.')
  }

  return context
}