import React, { useState, useEffect } from 'react';
import { IoChevronBackOutline } from 'react-icons/all';
import { Link } from "react-router-dom";

import './styles.css'
import { useProductData } from '../../../context/productContext';

import AsideBar from '../../../components/AsideBar'
import ProdutoView from '../../../components/ProdutosView'
import {
  fetchMediaType,
  fetchMediaStyles,
  fetchMediaTags,
  AdminAccess
} from '../../../services/api';

import { ModalEditProduct } from '../../../components/Modals/EditarProduto';

const filter = async (ProductDataBase, setProductDataView, mediaFilter) => {
  let newMediaData = ProductDataBase.media
  let newColorVariations = ProductDataBase.color_variations

  if (mediaFilter.type !== -1) {
    newMediaData = await newMediaData.filter(media => media.media_type === mediaFilter.type)

    newColorVariations = await newColorVariations.map(ColorMedia => {
      let newMediaCorlor = ColorMedia.media.filter(media => media.media_type === mediaFilter.type)
      return ({ ...ColorMedia, media: newMediaCorlor })
    })
  }

  if (mediaFilter.style !== -1) {
    newMediaData = await newMediaData.filter(media => media.style === mediaFilter.style)

    newColorVariations = await newColorVariations.map(ColorMedia => {
      let newMediaCorlor = ColorMedia.media.filter(media => media.style === mediaFilter.style)
      return ({ ...ColorMedia, media: newMediaCorlor })
    })
  }

  if (mediaFilter.tags !== -1) {
    newMediaData = await newMediaData.filter(media =>
      media.tags.map(tag => tag).includes(mediaFilter.tags)
    )

    newColorVariations = await newColorVariations.map(ColorMedia => {
      let newMediaCorlor = ColorMedia.media.filter(media =>
        media.tags.map(tag => tag).includes(mediaFilter.tags)
      )
      return ({ ...ColorMedia, media: newMediaCorlor })
    })
  }

  if (mediaFilter.color !== -1) {
    newColorVariations = await newColorVariations.filter(ColorMedia =>
      ColorMedia.id === mediaFilter.color
    )
  }

  setProductDataView(state => ({ ...state, media: newMediaData, color_variations: newColorVariations }))
}

const ProductView = () => {
  AdminAccess()

  const { contextProductsData } = useProductData()

  const [product, setProduct] = useState(null)
  const [view, setView] = useState(null)

  const [BackendMediaStyles, setBackendMediaStyles] = useState(null)
  const [BackendMediaTypes, setBackendMediaTypes] = useState(null)
  const [BackendMediaTags, setBackendMediaTags] = useState(null)

  const [mediaFilter, setMediaFilter] = useState({
    type: -1,
    style: -1,
    tags: -1,
    color: -1
  })

  useEffect(() => {
    if (contextProductsData.productView.length !== 0) {
      setProduct(contextProductsData.productView)
      setView(contextProductsData.productView)
    } else {
      setProduct(JSON.parse(localStorage.getItem('@greenishBook:PRODUCT_VIEW')))
      setView(JSON.parse(localStorage.getItem('@greenishBook:PRODUCT_VIEW')))
    }
  }, [])


  useEffect(() => {
    fetchMediaTags(setBackendMediaTags)
    fetchMediaStyles(setBackendMediaStyles)
    fetchMediaType(setBackendMediaTypes)
  }, [])

  useEffect(() => {
    product && filter(product, setView, mediaFilter)
  }, [product, mediaFilter])


  return (
    <div className="ProductView">
      <AsideBar className="aside-bar-web" />

      <div className="Container">
        <div className="ProductViewContent">
          <div className="Header">
            <Link to={`/produtos`}>
              <IoChevronBackOutline size={40} />
            </Link>
            <h1>Produto</h1>
          </div>
          <hr />

          <div className="ProductContent">
            <div className="ProductContainer">
              {view && <ProdutoView data={view ? view : {}} />}
            </div>
            <hr />
            <div className="ProductInfo">
              <span className="legend">Nome</span>
              <input className="input"
                placeholder={view ? view.name : "Nome do Produto"}
                disabled
              />
              <span className="legend">Coleção</span>
              <input className="input"
                placeholder={view ? view.collection.name : "Coleção do Produto"}
                disabled
              />


              <span className="legend">Categoria</span>
              <input className="input"
                placeholder={view ? view.category.name : "Categoria do Produto"}
                disabled
              />


              <span className="legend">Subcategoria</span>
              <input className="input"
                placeholder={view ? view.subcategory.name : "Subcategoria do Produto"}
                disabled
              />



              <hr />

              <h2>Filtros</h2>
              <select
                name="Tags"
                className="custom-select"
                onChange={e => setMediaFilter(state => ({ ...state, tags: parseInt(e.target.value) }))}
              >
                <option value={-1}>Selecione uma Tag</option>
                {BackendMediaTags && BackendMediaTags.map((tag) => {
                  return (
                    <option key={tag.id} value={tag.id}>{tag.name}</option>
                  )
                })}
              </select>

              <select
                name="Color"
                className="custom-select"
                onChange={e => setMediaFilter(state => ({ ...state, color: parseInt(e.target.value) }))}
              >
                <option value={-1}>Selecione uma Cor</option>
                {product && product?.color_variations.map((color) => {
                  return (
                    <option key={color.id} value={color.id}>{color.color}</option>
                  )
                })}
              </select>

              <select
                name="styles"
                className="custom-select"
                onChange={e => setMediaFilter(state => ({ ...state, style: parseInt(e.target.value) }))}
              >
                <option value={-1}>Selecione um Estilo</option>
                {BackendMediaStyles && BackendMediaStyles.map((style) => {
                  return (
                    <option key={style.id} value={style.id}>{style.name}</option>
                  )
                })}
              </select>

              <select
                name="colors"
                className="custom-select"
                onChange={e => setMediaFilter(state => ({ ...state, type: parseInt(e.target.value) }))}
              >
                <option value={-1}>Selecione um Tipo</option>
                {BackendMediaTypes && BackendMediaTypes.map((type) => {
                  return (
                    <option key={type.id} value={type.id}>{type.name}</option>
                  )
                })}
              </select>

              <div className="button">
                {product && <ModalEditProduct props={product} color={"#fff"} />}
              </div>

            </div>
          </div>
        </div>
      </div>

    </div>
  )
}

export default ProductView