import { createContext, useCallback, useContext, useEffect, useState } from "react";
import api, { URL_ACCESS } from "../services/api";

export const DataContext = createContext({})

export const DataProvider = (props) => {
  const [state, setState] = useState({
    medias: [],
    mediaView: [],
    BaseToFetchMediaURL: null,
    stateURL: {
      limit: 10,
      offset: 0,
      style: -1,
      type: -1,
      tags: -1,
    },
    lengthPagination: 0,
    properties: {
      types: [],
      styles: [],
      tags: [],
    }
  })

  useEffect(() => {
    const media = localStorage.getItem('@greenishBook:MEDIA')
    if (media?.length > 0) {
      setState(state => ({ ...state, medias: JSON.parse(media).medias }))
    }
  }, [])

  const createURLtoFetchMedia = useCallback(() => {
    let NewURL = `${URL_ACCESS}/api/media/?`
    const cases = {
      'limit': `limit=${state.stateURL.limit}`,
      'offset': `&offset=${(Number(state.stateURL.offset) - 1) * state.stateURL.limit}`,
      'style': `&style=${state.stateURL.style}`,
      'type': `&type=${state.stateURL.type}`,
      'tags': `&tags=${state.stateURL.tags}`,
    }
    NewURL += Number(state.stateURL.limit) !== -1 ? cases['limit'] : ''
    NewURL += Number(state.stateURL.offset) !== -1 ? cases['offset'] : ''
    NewURL += Number(state.stateURL.style) !== -1 ? cases['style'] : ''
    NewURL += Number(state.stateURL.type) !== -1 ? cases['type'] : ''
    NewURL += Number(state.stateURL.tags) !== -1 ? cases['tags'] : ''

    setState(state => ({ ...state, BaseToFetchMediaURL: NewURL }))
  }, [state.stateURL])

  const fetchMediaDataPagination = useCallback((MidiaPagination) => {
    if (!MidiaPagination) { return }
    api.get(MidiaPagination)
      .then(response => {
        const data = response.data
        const lengthPagination = Math.floor(data.count / state.stateURL.limit)
        const lengthMedia = data.count % state.stateURL.limit

        lengthMedia > 0
          ? setState(state => ({
            ...state,
            lengthPagination: lengthPagination + 1
          }))
          : setState(state => ({
            ...state,
            lengthPagination: lengthPagination
          }))

        setState(state => ({ ...state, medias: data.results }))
        localStorage.setItem('@greenishBook:MEDIA', JSON.stringify({
          medias: data.results,
        }))
      })
      .catch(err => {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      })
  }, [state.stateURL.limit])

  const fetchMediaProperties = useCallback(() => {
    if (
      state.properties.types.length !== 0 ||
      state.properties.styles.length !== 0
    ) { return }

    Promise.all([
      api.get("/properties/media/type/"),
      api.get("/properties/media/style/"),
      api.get("/tags")
    ]).then((res) => {
      setState(state => ({
        ...state, properties: {
          types: res[0].data,
          styles: res[1].data,
          tags: res[2].data,
        }
      }))
    })
  }, [state.properties])

  return (
    <DataContext.Provider value={{
      contextMediaData: state,
      setContextMediaData: setState,
      createURLtoFetchMedia,
      fetchMediaDataPagination,
      fetchMediaProperties,
    }}>
      {props.children}
    </DataContext.Provider>
  )
}

export const useData = () => {
  const context = useContext(DataContext)
  if (context === undefined) {
    throw new Error('useData necessita ser utilizado dentro de DataProvider.')
  }

  return context
}