import axios from "axios";

const useData = localStorage.getItem('@greenishBook:USER_DATA')
export const AdminToken = JSON.parse(useData)?.AdminToken

// export const URL_ACCESS = 'https://imagesapi.azurewebsites.net'
export const URL_ACCESS = 'https://gerenciador-de-imagems-teste.herokuapp.com'
export const URL_Location = 'http://localhost:3000'

const api = axios.create({
  baseURL: `${URL_ACCESS}/api`,
  headers: {
    'Content-Type': 'application/json',
    'Authorization': `${AdminToken ? `Basic ${AdminToken}` : null}`
  },
})

// Acesso Permitido 
export const AdminAccess = () => {
  const useData = localStorage.getItem('@greenishBook:USER_DATA')
  const AdminToken = JSON.parse(useData)?.AdminToken

  if (!AdminToken) {
    alert("Nenhum Login reconhecido, Voltando a Tela de login")
    window.location.assign('/');
  }
}

// POST Request
export const AddNewMidia = async (images, tipo, estilo, tags, isPrivate) => {
  tags = tags ? tags.map((tag) => tag.id) : null

  let completed = false
  images.length < 1
    ? alert("Por favor, Selecione alguma(s) Midia(s) para ser(em) Adicionada(s)")
    : completed = images.map((img, index) => {
      let formData = new FormData()

      formData.append('image', img)
      formData.append('media_type', tipo)
      formData.append('style', estilo)
      tags && formData.append('tags', tags)
      formData.append('is_private', isPrivate)

      api.post('/media/admin/', formData)
        .then((response) => {
          console.log(response)
        })
        .catch((err) => console.log("Erro: " + err))

      if (index === images.length) {
        return true
      }
      else {
        return false
      }
    })

  completed && setTimeout(() => window.location.reload(), 500);
}



// GET Request
export const fetchMediaType = async (setType) => {
  try {
    let response = await api.get("/properties/media/type/")
    response = response.data

    setType(response)

  } catch (err) {
    console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
  }
}

