import { IoChevronBackOutline } from 'react-icons/all';
import { Link } from "react-router-dom";

import { ModalEditMedia } from '../../../components/Modals/EditarMidia';
import MidiaImageView from '../../../components/MidiaImageView'
import { useData } from '../../../context/dataContext';
import { AdminAccess } from '../../../services/api';
import AsideBar from '../../../components/AsideBar'

import './styles.css'
import { useEffect, useState } from 'react';

const MidiaView = () => {
  AdminAccess()
  const { contextMediaData } = useData()
  const [media, setMedia] = useState(null)

  useEffect(() => {
    if (contextMediaData.mediaView.length !== 0) {
      setMedia(contextMediaData.mediaView)
    } else {
      setMedia(JSON.parse(localStorage.getItem('@greenishBook:MEDIA_VIEW')))
    }
  }, [])

  return (
    <div className="MidiaView">
      <AsideBar className="aside-bar-web" />
      <div className="Container">
        <div className="MidiaViewContent">
          <div className="Header">
            <Link to={`/midia`}>
              <IoChevronBackOutline size={40} />
            </Link>
            <h1>Mídia</h1>
          </div>
          <hr />

          <div className="MidiaContent">
            <div className="ImageContainer">
              {media ??
                media
                ? <MidiaImageView src={media.image} />
                : <MidiaImageView src={"https://secure.gravatar.com/avatar/36953a19fdecf7691949ad8598598ae2?s=96&d=https%3A%2F%2Fstatic.teamtreehouse.com%2Fassets%2Fcontent%2Fdefault_avatar-ea7cf6abde4eec089a4e03cc925d0e893e428b2b6971b12405a9b118c837eaa2.png&r=pg"} />
              }
            </div>
            <hr />
            <div className="MidiaInfo">

              <span>Tipo</span>
              <input
                placeholder={media
                  ? (media.media_type ? media.media_type.name : 'Midia sem tipo')
                  : "Midia sem tipo"
                }
                disabled
              />
              <span>Estilo</span>
              <input
                placeholder={media
                  ? (media.style ? media.style.name : 'Midia sem Estilo')
                  : "Estilo da midia"}
                disabled
              />
              <span>Tags</span>
              <input
                placeholder={media
                  ? media.tags.length > 0
                    ? media.tags.map((tag) => ` ${tag.name}`)
                    : 'Midia sem Tags'
                  : "Midia sem Tags"
                }
                disabled
              />

              <div className="button">
                {media && <ModalEditMedia props={media} />}
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default MidiaView