import { createContext, useCallback, useContext, useState } from "react";
import api, { URL_ACCESS } from "../services/api";

export const ProductContext = createContext({})

export const ProductDataProvider = (props) => {
    const [state, setState] = useState({
        products: [],
        productView: [],
        view: [],
        baseToFetchProductsURL: null,
        stateURL: {
            limit: 8,
            offset: 1,
        },
        lengthPagination: 0,
    })

    const createURLtoFetchProducts = useCallback(() => {
        let NewURL = `${URL_ACCESS}/api/products/?`
        const cases = {
            'limit': `limit=${state.stateURL.limit}`,
            'offset': `&offset=${(Number(state.stateURL.offset) - 1) * state.stateURL.limit}`,
        }
        NewURL += Number(state.stateURL.limit) !== -1 ? cases['limit'] : ''
        NewURL += Number(state.stateURL.offset) !== -1 ? cases['offset'] : ''

        setState(state => ({ ...state, BaseToFetchProductsURL: NewURL }))
    }, [state.stateURL])

    const fetchProductsDataPagination = useCallback((productPagination) => {
        if (!productPagination) { return }
        api.get(productPagination)
            .then(response => {
                const data = response.data
                const lengthPagination = Math.floor(data.count / state.stateURL.limit)
                const lengthProduct = data.count % state.stateURL.limit

                lengthProduct > 0
                    ? setState(state => ({
                        ...state,
                        lengthPagination: lengthPagination + 1
                    }))
                    : setState(state => ({
                        ...state,
                        lengthPagination: lengthPagination
                    }))

                setState(state => ({ ...state, products: data.results, view: data.results }))
            })
            .catch(err => {
                console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
            })
    }, [state.stateURL.limit])

    return (
        <ProductContext.Provider value={{
            contextProductsData: state,
            setContextProductsData: setState,
            createURLtoFetchProducts,
            fetchProductsDataPagination
        }}>
            {props.children}
        </ProductContext.Provider>
    )
}

export const useProductData = () => {
    const context = useContext(ProductContext)
    if (context === undefined) {
        throw new Error('useData necessita ser utilizado dentro de DataProvider.')
    }

    return context
}