import React, { useEffect, useState } from 'react'
import './styles.css'
import { FaBars } from 'react-icons/fa'

import OrderBy from '../OrderBy'

import api, {
  fetchMediaType,
  fetchMediaStyles,
  fetchMediaTags,
  URL_ACCESS
} from '../../services/api'

const createURLtoFetch = (stateURL, setBaseToFetchMediaURL) => {
  let NewURL = `${URL_ACCESS}/api/media/?`
  if (stateURL.limit !== -1) {
    NewURL += `limit=${stateURL.limit}`
  }
  if (stateURL.offset !== -1) {
    NewURL += `&offset=${(Number(stateURL.offset) - 1) * stateURL.limit}`
  }
  if (Number(stateURL.style) !== -1) {
    NewURL += `&style=${stateURL.style}`
  }
  if (Number(stateURL.type) !== -1) {
    NewURL += `&type=${stateURL.type}`
  }
  if (Number(stateURL.tags) !== -1) {
    NewURL += `&tags=${stateURL.tags}`
  }
  setBaseToFetchMediaURL(NewURL)
}

function NavBar() {
  const [BaseToFetchMediaURL, setBaseToFetchMediaURL] = useState(null)
  const [BackendMediaStyles, setBackendMediaStyles] = useState(null)
  const [BanckendMediaData, setBanckendMediaData] = useState(null)
  const [BackendMediaTypes, setBackendMediaTypes] = useState(null)
  const [BackendMediaTags, setBackendMediaTags] = useState(null)

  const [tamanhoDaPaginação, setTamanhoDaPaginação] = useState(0)

  const [stateURL, setStateULR] = useState({
    limit: 10,
    offset: 1,
    style: -1,
    type: -1,
    tags: -1,
  })

  useEffect(() => createURLtoFetch(stateURL, setBaseToFetchMediaURL), [stateURL])

  useEffect(() => {
    const fetchMediaDataEndPagination = async (MidiaPagination) => {
      try {
        let response = await api.get(MidiaPagination)
        response = response.data

        if ((response.count % stateURL.limit) > 0) {
          setTamanhoDaPaginação(Math.floor(response.count / stateURL.limit) + 1)
        } else {
          setTamanhoDaPaginação(Math.floor(response.count / stateURL.limit))
        }
        setBanckendMediaData(response.results)
      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }
    BaseToFetchMediaURL && fetchMediaDataEndPagination(BaseToFetchMediaURL)
  }, [BaseToFetchMediaURL])

  useEffect(() => {
    fetchMediaTags(setBackendMediaTags)
    fetchMediaStyles(setBackendMediaStyles)
    fetchMediaType(setBackendMediaTypes)
  }, [])

  const [sidebar, setSidebar] = useState(false);

  const showSidebar = () => setSidebar(!sidebar);

  return (
    <div className="navbar">
      <button
        onClick={showSidebar}
        className='menu-bars'
      >
        <FaBars size={30} />
      </button>

      <nav className={sidebar ? 'nav-menu active' : 'nav-menu'}>

        <button
          onClick={showSidebar}
          className='menu-bars'
        >
          <FaBars size={30} />
        </button>

        <div className="nav-menu-item">
          Filtros
          {BackendMediaTypes &&
            <OrderBy
              state={[setStateULR]}
              map={[BackendMediaTypes, BackendMediaStyles, BackendMediaTags]}
            />
          }
        </div>

      </nav>

    </div>
  )
}

export default NavBar;