import React, { useState } from 'react'
import { ModalBase } from '../ModalBase';
import { SelectionMidia } from '../SelectionMidia';
import { ImgRender } from '../../ImgRender';
import api from '../../../services/api';
import { useNavigate } from "react-router-dom";
import { useBookData } from '../../../context/bookContext'

import './style.css'


export const ModalEditarBook = ({ data }) => {
  const [value, setValue] = useState(data.media)
  const [name, setName] = useState(data.name)
  const [description, setDescription] = useState(data.description)
  const navigate = useNavigate()
  const { setContextBooksData } = useBookData()

  const UpdateBook = (name, description, medias, props) => {
    let newMediasIDs = medias.map((imgs) => imgs.id)

    let datas = {
      "name": name,
      "description": description,
      "media": newMediasIDs
    }

    api.patch(`/books/admin/${props.id}/`, datas)
      .then((response) => {
        if (response.status === 200) {
          setContextBooksData(state => ({
            ...state, bookView: {
              ...state.bookView,
              name: name,
              description: description,
              media: medias
            }
          }))
        }
      })
      .catch((err) => console.log("Erro: " + err))
      .then(setTimeout(() => navigate('/book'), 500))
  }

  return (
    <ModalBase title="Editar" subtitle='Editar Book' color={"#fff"}>
      <div className="Modal-EditBook">
        <div className="form">
          <div className="description" >
            <input
              type="text"
              placeholder="Nome do Book"
              defaultValue={name}
              onChange={(e) => { setName(e.target.value) }}
            />
            <textarea
              cols="30" rows="10"
              placeholder="Drescrição"
              defaultValue={description}
              onChange={(e) => { setDescription(e.target.value) }}
            />
          </div>

          <div className="MidiaSelection">
            <div className="Area-ViewsMedias">
              {
                value &&
                value.map((image, index) => {
                  return (
                    <ImgRender
                      key={index}
                      data={image}
                    />
                  )
                })
              }
            </div>
            <div className="Area-SelectMidias">
              <SelectionMidia dataa={[value, setValue]} />
            </div>
          </div>

        </div>
        <button onClick={() => {
          api.delete(`/books/admin/${data.id}/`)
            .then(setTimeout(() => navigate('/book'), 500))
        }} >
          Excluir
        </button>

        <button onClick={() => UpdateBook(name, description, value, data)}>Salvar</button>
      </div>
    </ModalBase>
  )
}