import React, { useEffect } from 'react'
import PaginationItem from '@material-ui/lab/PaginationItem';
import { makeStyles } from "@material-ui/core/styles";
import Pagination from '@material-ui/lab/Pagination';
import { Link } from 'react-router-dom'

import './styles.css'
import { useProductData } from '../../../context/productContext'

import MenuCategory from '../../../components/MenuCategory'
import { AdminAccess } from '../../../services/api';
import ImgProduto from '../../../components/ImgProduto';
import AsideBar from '../../../components/AsideBar'
import Search from '../../../components/Search';

const useStyles = makeStyles(() => ({
  root: {
    "& .Mui-selected": {
      background: "#00cc6d",
      color: "white",
      "&:hover": {
        background: "#c4f5d0",
        color: "black"
      }
    },
    "& .MuiPaginationItem-page:hover": {
      background: "#c4f5d0",
      opacity: 0.9
    }
  }
}));


const ProductsPage = () => {
  AdminAccess()

  const {
    contextProductsData,
    setContextProductsData,
    createURLtoFetchProducts,
    fetchProductsDataPagination
  } = useProductData()

  useEffect(() => {
    createURLtoFetchProducts()
  }, [createURLtoFetchProducts])

  useEffect(() => {
    fetchProductsDataPagination(contextProductsData.BaseToFetchProductsURL)
  }, [contextProductsData.BaseToFetchProductsURL, fetchProductsDataPagination])

  const classes = useStyles();
  return (
    <div className="ProductsPage">
      <AsideBar className="aside-bar-web" />
      <div className="Container">
        <div className="Top-Menu">
          <div className="CategoryMenu">
            <MenuCategory title="Produto" />
            <div className="Search">
              {contextProductsData && <Search data={[contextProductsData.products, setContextProductsData]} />}
            </div>
          </div>
        </div>
        <div className="Content-App">
          <div className="content-midia-views">
            {contextProductsData &&
              contextProductsData.view.map((produto, index) => (
                <Link key={index} to={`/produtosview`} onClick={() => {
                  setContextProductsData(state => ({ ...state, productView: produto }))
                  localStorage.setItem('@greenishBook:PRODUCT_VIEW', JSON.stringify(produto))
                }}>
                  <ImgProduto
                    key={produto.id}
                    src={produto.media[0] ? produto.media[0].image : './assets/images/SemMidia.svg'}
                    title={produto.name}
                  />
                </Link>
              ))
            }
          </div>
          <div className="Content-Pagination">
            <Pagination
              onChange={(_, v) => setContextProductsData(state => ({ ...state, stateURL: { limit: 8, offset: v } }))}
              page={contextProductsData.stateURL.offset}
              className={classes.root}
              count={contextProductsData.lengthPagination}
              boundaryCount={2}
              renderItem={(item) => (
                <PaginationItem
                  style={{
                    fontSize: 12,
                    fontWeight: 'bold',
                  }}
                  {...item} />
              )}
            />
          </div>
        </div>
      </div>
    </div>
  )
}

export default ProductsPage