import PaginationItem from '@material-ui/lab/PaginationItem'
import { makeStyles } from "@material-ui/core/styles"
import Pagination from '@material-ui/lab/Pagination'
import { Link } from "react-router-dom"

import MenuCategory from '../../../components/MenuCategory'
import AsideBar from '../../../components/AsideBar'
import OrderBy from '../../../components/OrderBy'
import NavBar from '../../../components/NavBar'
import { AdminAccess } from '../../../services/api'

import './styles.css'
import { useData } from '../../../context/dataContext'
import { useEffect } from 'react'

const useStyles = makeStyles(() => ({
  root: {
    "& .Mui-selected": {
      background: "#00cc6d",
      color: "white",
      "&:hover": {
        background: "#c4f5d0",
        color: "black"
      }
    },
    "& .MuiPaginationItem-page:hover": {
      background: "#c4f5d0",
      opacity: 0.9
    }
  }
}))

const MediaPage = () => {
  AdminAccess()
  const {
    contextMediaData,
    setContextMediaData,
    createURLtoFetchMedia,
    fetchMediaDataPagination
  } = useData()

  useEffect(() => {
    createURLtoFetchMedia()
  }, [createURLtoFetchMedia])

  useEffect(() => {
    fetchMediaDataPagination(contextMediaData.BaseToFetchMediaURL)
  }, [contextMediaData.BaseToFetchMediaURL, fetchMediaDataPagination])

  const classes = useStyles();
  return (
    <div className="LandingPage">
      <AsideBar className="aside-bar-web" />
      <div className="Container">
        <div className="Top-Menu">
          <div className="CategoryMenu">
            <MenuCategory title="Mídia" />
            <div className="sidebar">
              <NavBar />
            </div>
          </div>
          <div className="OrderMenu">
            <OrderBy />
          </div>
        </div>

        <div className="Content-App">
          <div className="content-midia-views">
            {contextMediaData?.medias.map(media => (
              <Link key={media.id} to={`/midiaview`} onClick={() => {
                setContextMediaData(state => ({ ...state, mediaView: media }))
                localStorage.setItem('@greenishBook:MEDIA_VIEW', JSON.stringify(media))
              }}>
                <img src={media.image} alt={`ImageId: ${media.id}`} />
              </Link>
            ))}
          </div>
          <div className="Content-Pagination">
            <Pagination
              onChange={(_, v) => {
                setContextMediaData(state => ({
                  ...state,
                  stateURL: { ...state.stateURL, offset: v }
                }))
              }}
              page={contextMediaData.stateURL.offset}
              className={classes.root}
              count={contextMediaData.lengthPagination}
              boundaryCount={1}
              renderItem={(item) => (
                <PaginationItem
                  style={{
                    fontSize: 12,
                    fontWeight: 'bold',
                  }}
                  {...item}
                />
              )}
            />
          </div>
        </div>
      </div>
    </div>
  )
}


export default MediaPage