import React from 'react'
import './style.css';

const NewPassword = () => {
    return (
        <div className="NewPasswordContainer">
            <div className="NewPasswordContent">
                <div className="LogoContainer">
                    <img src="./assets/images/logo-vertical.png" alt="Logo Greenish" />
                </div>

                <div className="FormContent">
                    <h1>
                        Insira sua nova senha!
                    </h1>

                    <input type="password" id="password" placeholder="Nova senha" />
                    <input type="password" id="password" placeholder="Repita a sua nova senha" />
                </div>

                <a href="/login" className="button">Nova Senha</a>
            </div>
        </div>
    )
}

export default NewPassword