import { Link } from "react-router-dom";

import './styles.css';

const AccessType = () => {

  return (
    <div id="page-access">
      <div className="container">

        <div className="logo-container">
          <img src="./assets/images/logo-vertical.png" alt="Logo Greenish" />
        </div>

        <div className="access-content">
          <Link className='convidado' to={'/cltprodutos'}>
            <div className="convidadoimg">
              <img src='./assets/images/PersonIco.png' alt="Icone de Personagem" />
            </div>
            <span>Convidado</span>
          </Link>
          <Link className='gerente' to={'/login'}>
            <div className="gerenteimg">
              <img src='./assets/images/PersonIco.png' alt="Icone de Personagem" />
            </div>
            <span>Administrador</span>
          </Link>
        </div>

      </div>
    </div>
  )
}

export default AccessType