import { useEffect } from 'react'
import { useData } from '../../context/dataContext'
import './style.css'

function OrderBy() {
  const {
    contextMediaData,
    setContextMediaData,
    fetchMediaProperties
  } = useData()
  useEffect(() => fetchMediaProperties(), [fetchMediaProperties])

  const changeProperty = (property, value) => {
    setContextMediaData(state =>
    ({
      ...state,
      stateURL: { ...state.stateURL, [property]: value, offset: 0 }
    }))
  }

  return (
    <div className="OrderBy">
      <select
        value={contextMediaData.stateURL.type}
        onChange={e => changeProperty('type', e.target.value)}
      >
        <option value={-1}>Selecione um Tipo</option>
        {contextMediaData?.properties.types.map((position, index) =>
          <option key={index} value={position.id}>{position.name}</option>
        )}
      </select>

      <select
        value={contextMediaData.stateURL.style}
        onChange={e => changeProperty('style', e.target.value)}
      >
        <option value={-1}>Selecione um Estilo</option>
        {contextMediaData?.properties.styles.map((position, index) =>
          <option key={index} value={position.id}>{position.name}</option>
        )}
      </select>

      <select
        value={contextMediaData.stateURL.tags}
        onChange={e => changeProperty('tags', e.target.value)}
      >
        <option value={-1}>Selecione uma Tag</option>
        {contextMediaData?.properties.tags.map((position, index) =>
          <option key={index} value={position.id}>{position.name}</option>
        )}
      </select>
    </div >
  )
}

export default OrderBy