import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import { DataProvider } from './context/dataContext';
import { BookDataProvider } from './context/bookContext'
import { ProductDataProvider } from './context/productContext';

ReactDOM.render(
  <DataProvider>
    <BookDataProvider>
      <ProductDataProvider>
        <React.StrictMode>
          <App />
        </React.StrictMode>
      </ProductDataProvider>
    </BookDataProvider>
  </DataProvider>,
  document.getElementById('root')
);