import React, { useState, useEffect } from 'react';
import { IoChevronBackOutline } from 'react-icons/all';
import { Link } from "react-router-dom";

import BookImageView from '../../../components/BookImageView'
import { useBookData } from '../../../context/bookContext';

import './styles.css'


const ClienteBookView = () => {
    const {
        contextBooksData
    } = useBookData()

    const [LoadMediaBook, setLoadMediaBook] = useState(0)
    const [book, setBook] = useState(null)

    useEffect(() => {
        if (contextBooksData.bookView.length !== 0) {
            setBook(contextBooksData.bookView)
        } else {
            setBook(JSON.parse(localStorage.getItem('@greenishBook:BOOK_VIEW')))
        }
    }, [])

    return (
        <div className="ClienteBookView">
            <div className="Container">
                <div className="BookViewContent">
                    <div className="Header">
                        <Link to={`/cltbook`}>
                            <IoChevronBackOutline size={40} />
                        </Link>
                        <h1>Book</h1>
                    </div>
                    <hr />

                    <div className="BookContent">
                        <div className="BookContainer">
                            {book &&
                                book.media[LoadMediaBook]
                                ? <BookImageView src={book.media[LoadMediaBook].image} />
                                : <BookImageView src='./assets/images/BookSemMidia.svg' />
                            }

                            <div className="book-midias">
                                {book &&
                                    book.media.map((media, index) => {
                                        if (index === LoadMediaBook) return null
                                        return (
                                            <img
                                                key={index}
                                                src={media.image} alt=""
                                                onClick={() => { setLoadMediaBook(index) }}
                                            />
                                        )
                                    })}
                            </div>
                        </div>
                        <hr />
                        <div className="BookInfo">

                            <span>Nome</span>
                            <input
                                placeholder={book ? book.name : "Nome do Book"}
                                disabled
                            />
                            <span>Descrição</span>
                            <textarea
                                disabled
                                rowns="10"
                                value={book ? book.description : "Descrição do Book"}
                            />

                        </div>


                    </div>
                </div>
            </div>

        </div>
    )
}

export default ClienteBookView