import React, { useState } from "react";
import axios from "axios";

import './styles.css';
import { URL_ACCESS } from "../../services/api";
import { useNavigate } from "react-router-dom";

const data = {
  name: '',
  email: '',
  isAdmin: false,
  AdminToken: '',
};

const Login = () => {
  const navigate = useNavigate()
  const [email, setEmail] = useState('')
  const [senha, setSenha] = useState('')

  function logIn(email, senha, context) {
    const baseURL = `${URL_ACCESS}/users/login/`
    const config = {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    }
    let datas = new FormData()

    datas.append('email', email)
    datas.append('password', senha)

    axios
      .post(baseURL, datas, config)
      .then((res) => {
        context = {
          ...context,
          email: res.data.email,
          isAdmin: res.data.is_staff,
          name: res.data.first_name
        }
        CreateToken(email, senha, context)
      })
      .catch(() => {
        alert("As informações estão erradas. Por favor verifique!")
      })

  }


  function CreateToken(email, senha, context) {
    let accessToken = Buffer.from(`${email}:${senha}`).toString('base64')
    context = {
      ...context,
      AdminToken: accessToken
    }

    localStorage.setItem('@greenishBook:USER_DATA', JSON.stringify(context))

    navigate(`/midia`)
  }

  return (
    <div id="page-login">
      <div className="container">

        <div className="logo-container">
          <img src="./assets/images/logo-vertical.png" alt="Logo Greenish" />
        </div>

        <form className="form-container">
          <div className="input-block">
            <input
              type="email"
              id="email"
              placeholder="Email"
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>

          <div className="input-block">
            <input
              type="password"
              id="password"
              placeholder="Senha"
              onChange={(e) => setSenha(e.target.value)}
            />
          </div>
        </form>

        <button
          className="button"
          onClick={() => { logIn(email, senha, data) }}
        >Entrar</button>

      </div>
    </div>
  )
}

export default Login