import { BrowserRouter, Routes, Route } from 'react-router-dom'

import AccessType from './pages/AccessType';
import BookPage from './pages/Book/BookPage';
import BookView from './pages/Book/BookView';
import BookCliente from './pages/Book/CltBookPage';
import ClienteBookView from './pages/Book/CltBookView';
import ForgotPassword from './pages/ForgotPassword';
import Login from './pages/Login';
import MediaPage from './pages/Midia/MidiaPage';
import MidiaView from './pages/Midia/MidiaView';
import NewPassword from './pages/NewPassword';
import ProductViewCliente from './pages/Produtos/CltProductsView';
import ProdutoCliente from './pages/Produtos/CltProdutoPage';
import ProductsPage from './pages/Produtos/ProductsPage';
import ProductView from './pages/Produtos/ProductsView';
import Register from './pages/Register';

export const App = () => (
  <BrowserRouter>
    <Routes>
      <Route path='/' element={<AccessType />} />
      <Route path='/login' element={<Login />} />
      <Route path='/register' element={<Register />} />
      <Route path='/forgotpassword' element={<ForgotPassword />} />
      <Route path='/newpassword' element={<NewPassword />} />

      <Route path='/midia' element={<MediaPage />} />
      <Route path='/midiaview' element={<MidiaView />} />

      <Route path='/produtos' element={<ProductsPage />} />
      <Route path='/cltprodutos' element={<ProdutoCliente />} />
      <Route path='/produtosview' element={<ProductView />} />
      <Route path='/cltprodutosview' element={<ProductViewCliente />} />

      <Route path='/book' element={<BookPage />} />
      <Route path='/cltbook' element={<BookCliente />} />
      <Route path='/bookview' element={<BookView />} />
      <Route path='/cltbookview' element={<ClienteBookView />} />
    </Routes>
  </BrowserRouter>
);


export default App;